import React from 'react';
import s from './MainPanel.module.css';
import ServicePanel from '../ServicePanel/ServicePanel';
import FiltrPanel from '../FiltrPanel/FiltrPanel';

import {filtrs} from '../../data/FiltrData/FiltrData'

const createServicePanel = () => { 
  return  filtrs.map( p => <ServicePanel name={p.name} caption={p.caption} key={p.id}/>);   
}

const MainPanel = () => {

  return (
    <div className={s.mainPanel}>      
      <FiltrPanel />          
      {createServicePanel()}
    </div>
  );
}

export default MainPanel;